import Vue from 'vue'
import App from './App'
import router from './router'

import VSwipe from 'vswipe'
Vue.use(VSwipe)

import BackButton from '@/components/BackButton'
Vue.component('back-button', BackButton)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



